import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let lifeData = {};

try{
	// lifeDataAPP
	lifeData = uni.getStorageSync('lifeData');
}catch(e){
	
}

// APPstate
let saveStateKeys = ['vuex_username','vuex_avatar','vuex_gender','vuex_phone', 'vuex_token'];

// 
const saveLifeData = function(key, value){
	// 
	if(saveStateKeys.indexOf(key) != -1) {
		// lifeData
		let tmp = uni.getStorageSync('lifeData');
		// APPlifeData{}
		tmp = tmp ? tmp : {};
		tmp[key] = value;
		// lifeData
		uni.setStorageSync('lifeData', tmp);
	}
}
const store = new Vuex.Store({
	// 
	state: {
		// lifeDatastate
		// vuex_
		vuex_username: lifeData.vuex_username ? lifeData.vuex_username : '',
		vuex_avatar: lifeData.vuex_avatar ? lifeData.vuex_avatar : '',
		vuex_gender: lifeData.vuex_gender ? lifeData.vuex_gender : '',
		vuex_phone: lifeData.vuex_phone ? lifeData.vuex_phone : '',
		vuex_token: lifeData.vuex_token ? lifeData.vuex_token : '',
		vuex_fridge:'',
		vuex_editFood:'',
		vuex_viewFood:{},
		vuex_viewDish:{},
		// vuex_versionlifeData.vuex_version
		vuex_version: '1.0.1'
	},
	mutations: {
		$uStore(state, payload) {
			// stateuser.info.score = 1
			let nameArr = payload.name.split('.');
			let saveKey = '';
			let len = nameArr.length;
			if(nameArr.length >= 2) {
				let obj = state[nameArr[0]];
				for(let i = 1; i < len - 1; i ++) {
					obj = obj[nameArr[i]];
				}
				obj[nameArr[len - 1]] = payload.value;
				saveKey = nameArr[0];
			} else {
				// state
				state[payload.name] = payload.value;
				saveKey = payload.name;
			}
			// 
			saveLifeData(saveKey, state[saveKey])
		}
	}
})

export default store